	module.exports = {"data":{"markdownRemark":{"html":"<p>Do you have a heart rate monitor that seems to be acting odd and you think the battery is good? Especially when you first start riding or on very dry, windy days? Chances are you have dried salt on the snaps of the monitor and strap. Try cleaning them with some water or even licking them clean if clean water is not available. I know, yuck, but what is more important to you, being a little gross or the data? Thought so.</p>\n<p>Why? Salt is a great conductor when mixed with water, but it is terrible when dry. Your sweat gets in there and drys out and causes chaos.</p>","frontmatter":{"title":"Odd Heart Rate Readings","date":"2018-02-10T00:20:00.000Z","path":"/odd-readings-from-heart-rate-monitors/","parent":"/home/hypersprite/Documents/code/araceathlete-gatsby-material-ui-v1/src/pages/blog-posts/odd-heart-rate-readings.md absPath of file","tags":"training, tipsandtricks, heartrate","description":null,"cover":null}}},"pathContext":{"slug":"/blog-posts/odd-heart-rate-readings/"}}

